import {
  LocalStoragePremiumKey,
  getLocalStoragePremiumData,
} from '../../app/storage/local/premium';
import {
  type OpenPopupPaymentMessage,
  type OpenPopupMessage,
} from '../../app/messages/shared';
import { Actions } from '../../app/actions/shared';
import { getConfigsUrl } from 'src/utils';

const successPremiumUrl = await getConfigsUrl(process.env.SUCCESS_PREMIUM_URL);

const url = location.origin + location.pathname;

if (successPremiumUrl && url === successPremiumUrl.toString()) {
  const idContainer = document.getElementsByClassName('putcodehere')[0];

  const id = idContainer.firstChild;
  if (id instanceof HTMLParagraphElement) {
    const { userId } = await getLocalStoragePremiumData(
      LocalStoragePremiumKey.UserId,
    );
    if (userId) {
      id.innerText = userId;
    }
  }
}

const linkContainers = document.getElementsByClassName('premiumpayment');

for (const linkContainer of linkContainers) {
  const link = linkContainer.querySelector('a');
  if (link instanceof HTMLAnchorElement) {
    link.addEventListener('click', handleClickPayPremium);
  }
}

const freeTrialLinks = document.getElementsByClassName('freetrialwebclick');

for (const link of freeTrialLinks) {
  if (link instanceof HTMLAnchorElement) {
    link.addEventListener('click', handleClickFreeTrial);
  }
}

async function handleClickFreeTrial(event: MouseEvent): Promise<void> {
  event.preventDefault();
  event.stopPropagation();

  const { hasPremium } = await getLocalStoragePremiumData(
    LocalStoragePremiumKey.HasPremium,
  );

  if (!hasPremium) {
    await chrome.runtime.sendMessage<OpenPopupMessage>({
      action: Actions.OpenPopup,
    });
  }
}

async function handleClickPayPremium(event: MouseEvent): Promise<void> {
  event.preventDefault();
  event.stopPropagation();

  await chrome.runtime.sendMessage<OpenPopupPaymentMessage>({
    action: Actions.OpenPopupPayment,
  });
}
